import './Workspace.scss'
import {connect} from "react-redux";
import {NavLink} from "react-router-dom";
import React from "react";
import analytics from './../../sources/svg/analytics.svg'
import simulator from './../../sources/svg/simulator.svg'
import dashboard from './../../sources/svg/dashboard.svg'
import game from './../../sources/svg/game.svg'

function Workspace(props: Props) {
// ================================= STATE =================================

// ================================= FUNCTIONS =================================

// ================================= LAYOUT =================================
  return (
    <div className="workspace">
      <NavLink to="analytics" className="menu-button">
        <div></div>
        <img src={analytics} alt="analytics"/>
      </NavLink>
      <NavLink to="simulator" className="menu-button">
        <div></div>
        <img src={simulator} alt="analytics"/>

      </NavLink>
      <NavLink to="dashboards" className="menu-button">
        <div></div>
        <img src={dashboard} alt="analytics"/>

      </NavLink>
      <NavLink to="game" className="menu-button">
        <div></div>
        <img src={game} alt="analytics"/>

      </NavLink>
    </div>
  );
}

// ================================= PROPS =================================

interface Props {

}

interface IMapStoreToProps {

}

interface IMapDispatchToProps {
  reduxActions: {},
}


// ================================= REDUX =================================

//    store  props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {}
}

//   props  reduxActions,        store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {}
  }
}

// ================================= EXPORT =================================

//      ,       ,     mapStoreToProps  mapDispatchToProp
//       ,    mapStoreToProps  mapDispatchToProp
//                

export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(Workspace);